/* ====================================
// css variables
// ==================================== */

@import url("https://fonts.googleapis.com/css2?family=Codystar&display=swap");

:root {
  --cube-size: 100vh;
}

/* ====================================
// reset rules
// ==================================== */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;

  font-size: 1rem;
  line-height: 1.25;
}

a {
  color: inherit;
}

img,
video {
  max-inline-size: 100%;
  block-size: auto;
  vertical-align: middle;
}

/* ====================================
// base rules
// ==================================== */

.scene {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  perspective: calc(var(--cube-size) + 400px);
  background: rgb(51, 38, 144);
  background: radial-gradient(
    circle,
    rgba(51, 38, 144, 1) 0%,
    rgba(36, 0, 75, 1) 74%,
    rgba(30, 0, 43, 1) 100%
  );
}

.cube {
  animation: cube-appear 1s ease-in-out infinite;
  position: absolute;
  width: var(--cube-size);
  height: var(--cube-size);
  left: 50vw;
  top: 50vh;
  margin-top: calc(-1 / 2 * var(--cube-size));
  margin-left: calc(-1 / 2 * var(--cube-size));
  transform-style: preserve-3d;
  transform: translateZ(calc(-1 / 2 * var(--cube-size)));
  transition: transform 0.2s;
  text-align: center;
}

.cube__face {
  position: absolute;
  line-height: var(--cube-size);
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 400px;
  font-family: sans-serif;
  backface-visibility: hidden;
  color: #fff;
}

.cube__face--front {
  transform: rotateY(0deg) translateZ(calc(var(--cube-size) / 2));
}

.card {
  color: #fff;
  position: absolute;
  bottom: 10%;
  left: 40%;
  font-family: sans-serif;
  animation: card-appear 0.5s ease-in-out 1;
}

.card h1 {
  margin-bottom: 20px;
}

.card a {
  color: #000;
  background: #fff;
  padding: 7px 20px;
  border-radius: 5px;
  text-decoration: none;
  transition: padding 0.3s;
}

.card a:hover {
  padding: 7px 50px;
}

@keyframes card-appear {
  from {
    transform: translatex(-5%);
    opacity: 0%;
  }
  to {
    transform: translatex(0%);
    opacity: 100%;
  }
}

@keyframes cube-appear {
  from {
    opacity: 100%;
  }
  to {
    opacity: 100%;
  }
}
