@import "https://fonts.googleapis.com/css2?family=Codystar&display=swap";
:root {
  --cube-size: 100vh;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizelegibility;
  font-size: 1rem;
  line-height: 1.25;
}

a {
  color: inherit;
}

img, video {
  max-inline-size: 100%;
  block-size: auto;
  vertical-align: middle;
}

.scene {
  perspective: calc(var(--cube-size)  + 400px);
  background: radial-gradient(circle, #332690 0%, #24004b 74%, #1e002b 100%);
  position: absolute;
  inset: 0;
}

.cube {
  width: var(--cube-size);
  height: var(--cube-size);
  margin-top: calc(-1 / 2 * var(--cube-size));
  margin-left: calc(-1 / 2 * var(--cube-size));
  transform-style: preserve-3d;
  transform: translateZ(calc(-1 / 2 * var(--cube-size)) );
  text-align: center;
  transition: transform .2s;
  animation: cube-appear 1s ease-in-out infinite;
  position: absolute;
  top: 50vh;
  left: 50vw;
}

.cube__face {
  line-height: var(--cube-size);
  width: 100%;
  height: 100%;
  text-align: center;
  backface-visibility: hidden;
  color: #fff;
  font-family: sans-serif;
  font-size: 400px;
  position: absolute;
}

.cube__face--front {
  transform: rotateY(0deg) translateZ(calc(var(--cube-size) / 2) );
}

.card {
  color: #fff;
  font-family: sans-serif;
  animation: card-appear .5s ease-in-out;
  position: absolute;
  bottom: 10%;
  left: 40%;
}

.card h1 {
  margin-bottom: 20px;
}

.card a {
  color: #000;
  background: #fff;
  border-radius: 5px;
  padding: 7px 20px;
  text-decoration: none;
  transition: padding .3s;
}

.card a:hover {
  padding: 7px 50px;
}

@keyframes card-appear {
  from {
    opacity: 0;
    transform: translateX(-5%);
  }

  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes cube-appear {
  from {
    opacity: 1;
  }

  to {
    opacity: 1;
  }
}

